@import url(https://fonts.googleapis.com/css2?family=Commissioner:wght@400;500;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Inter:wght@400;500;700&display=swap);
/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/

html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed, 
figure, figcaption, footer, header, hgroup, 
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, 
footer, header, hgroup, menu, nav, section {
	display: block;
}
body {
	line-height: 1;
}
ol, ul {
	list-style: none;
}
blockquote, q {
	quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
	content: '';
	content: none;
}
table {
	border-collapse: collapse;
	border-spacing: 0;
}
html {
  /*scroll-behavior: smooth;*/
  font-size: 15px;
  color: #fff;
}

* {
  box-sizing: border-box;
}

body {
  background-color: #181818;
  background-image: url("https://www.transparenttextures.com/patterns/carbon-fibre.png");
  /* This is mostly intended for prototyping; please download the pattern and re-host for production environments. Thank you! *//* This is mostly intended for prototyping; please download the pattern and re-host for production environments. Thank you! */
  font-family: "Commissioner", "Helvetica", "Segoe UI", sans-serif;
}

body *:focus {
  outline-style: dashed;
  outline-width: 1px;
  outline-offset: 2px;
  outline-color: #fff;
}

main {
  margin: 0 auto 0 auto;
  width: 62.8%;
}

@media only screen and (max-width: 900px) {
  main {
    width: 90%;
  }
}

a,
a:link,
a:visited,
a:hover,
a:active {
  text-decoration: none;
}

p {
  font-family: "Inter";

}

nav {
  position: fixed;
  z-index: 1;
  top: 0;
  display: grid;
  grid-template-columns: auto 0.19fr 0.38fr 0.19fr auto;
  grid-template-rows: auto 0px;
  height: 90px;
  width: 100%;
  background-color: #181818;
  border-bottom: 1px solid #333;
  cursor: default;
}

.nav-button-clicked {
  border-radius: 10px;
  display: inline-block;
  border: 0;
  padding: 4px 0 6px 0;
  font-family: inherit;
  font-weight: 500;
  text-align: center;
  color: #181818;
  background: linear-gradient(
    90deg,
    rgba(75, 172, 181, 1) 0%,
    rgba(0, 212, 255, 1) 100%
  );
  cursor: pointer;
}

.nav-button-unclicked {
  border: 0;
  display: inline-block;
  padding: 4px 0 6px 0;
  font-family: inherit;
  font-weight: 500;
  text-align: center;
  font-size: 1rem;
  background-color: inherit;
  opacity: 0.8;
  cursor: pointer;
}

.nav-logo {
  grid-column: 2/3;
  margin: auto;
  font-size: 1rem;
  color: #fff;
  opacity: 0.8;
  font-weight: 900;
  cursor: default;
}

.nav-sections {
  display: grid;
  width: 100%;
  margin: auto 0;
  grid-template-columns: repeat(3, 1fr);
  grid-template-rows: 1fr 1fr;
  grid-row-gap: 2px;
}

.nav-progress-bar {
  height: 2px;
  background: linear-gradient(90deg, #234e52 0%, rgba(0, 212, 255, 1) 100%);
  border-radius: 2px;
  width: 100%;
  grid-column: 1/-1;
  grid-row: 2/3;
  justify-self: start;
  transform-origin: left;
}

.social-link-wrapper {
  margin: auto;
}

.social-link {
  display: inline-block;
  margin: 0 8px;
  font-size: 2rem;
  color: #fff;
  opacity: 0.8;
}

@media only screen and (max-width: 1500px) {
  nav {
    height: 200px;
    grid-template-columns: auto 0.19fr 0.45fr 0.19fr auto;

  }

  .nav-sections {
    display: grid;
    width: 100%;
    margin: auto 0;
    grid-template-columns: 1fr;
    grid-template-rows: repeat(6, 1fr);
    grid-row-gap: 2px;
  }
}
@media only screen and (max-width: 660px) {
  .nav-logo,
  .social-link-wrapper {
    opacity: 0;
  }
}

.introduction {
  display: flex;
  color: #fff;
  padding-top: 125px;
  padding-left: 15px;
  cursor: default;
}

.introduction img {
  border-radius: 150px;
  transition: all 0.3s;
}

.introduction-img-scrolled {
  position: fixed;
  top: 13px;
  left: 14%;
  z-index: 2;
  height: 64px;
  width: 64px;
  transition: all 0.3s;
}

.introduction section {
  margin: auto 35px auto 35px;
  line-height: 1.5;
}

.introduction header {
  margin-bottom: 5px;
  margin-left: -25px;
  text-transform: uppercase;
  font-size: 2rem;
  font-weight: 800;
  background: linear-gradient(
    90deg,
    rgb(60, 138, 145) 0%,
    rgb(255, 255, 255) 50%,
    rgb(60, 138, 145) 100%
  );
  -webkit-background-clip: text;
          background-clip: text;
  background-size: 500%;
  -webkit-text-fill-color: transparent;
}

.introduction header:hover::after {
  content: " !";
}

.introduction-status::before,
.introduction-pulse::before {
  content: "";
  display: inline-block;
  height: 12px;
  width: 12px;
  border-radius: 20px;
  transform: translateY(1px);
  background-color: rgb(51, 122, 42);
}

.introduction-pulse::before {
  background-color: rgba(51, 122, 42, 0);
}

.introduction-status,
.introduction-pulse {
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  margin-top: 20px;
  padding: 5px 15px;
  border-radius: 20px;
  background-color: rgba(138, 138, 138, 0.25);
}

@media only screen and (max-width: 1500px) {
  .introduction {
    padding-top: 235px;
  }

  .introduction-img-scrolled {
    height: 94px;
    width: 94px;
    top: 53px;
  }
}

@media only screen and (max-width: 1200px) {
  .introduction {
    flex-direction: column;
  }

  .introduction > img {
    margin-bottom: 25px;
  }
}

@media only screen and (max-width: 700px) {
  .introduction-img-scrolled {
    height: 64px;
    width: 64px;
    top: 68px;
  }
}

.entry-card {
    display: flex;
    flex-direction: column;
    border-radius: 25px;
    margin: 25px 15px 0 15px;
}

.entry-card-main {
    border-radius: 25px 25px 0 0;
    background: rgba(75, 172, 181, 1);
    padding: 17px 20px;
    flex-grow: 1;
}

.entry-card-more {
    border-radius: 0 0 25px 25px;
    padding: 17px 20px;
    font-size: 0.95rem;
    background: #E6FFFA;
    color: #234E52;
}

.entry-title {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}

.entry-title > button {
    display: flex;
    border: none;
    border-radius: 15px;
    transform: translate(7px,-7px);
    height: 30px;
    width: 30px;
    cursor: pointer;
}

.entry-title > button > * {
    margin: auto;
    color: #234E52;
}


.entry-title-title {
    font-size: 0.95rem;
    font-weight: 700;
    margin-bottom: 10px;
    text-transform: uppercase;
    transform: translate(0,2px);
}

.entry-card-est {
    text-transform: uppercase;
    margin-bottom: 8px;
    font-weight: 700;
}

.entry-card-loc {
    font-size: 0.95rem;
}

.entry-description {
    line-height: 1.3;
    font-weight: 500;
    width: 95%;
    transform-origin: top;
}

.entry-card-kw {
    display: inline-block;
    margin: 8px 3px 0 3px;
    padding: 5px 8px;
    border-radius: 15px;
    background-color: rgba(75, 172, 181, 1);
    color: #fff;
    font-weight: 700;
}

.entry-card-kw-label::before {
    content: '#';
}

#i05 > div {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}

#i05 .entry-card-more {
  border-radius: 25px;
  flex-grow: 1;
}

/* compétences majeures */

#i03 > div {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}


#i03 > div > *:first-child {
  flex: 1 1;
}

#i03 > div > *:nth-child(0n+2) {
  flex: 1 1;
}

#i03 > div > *:nth-child(0n+3) {
  flex: 2 1;
}

#i03 > div > *:nth-child(0n+4) {
  flex: 1 1;
}

.cv-section {
    margin-left: 25px;
    margin-right: 25px;
    margin-top: 115px;
    padding-top: 115px;
    margin-bottom: -115px;
    cursor: default;
}

.cv-section::before {
    content: '';
    display: block;
}

.cv-section::after{
    content: '';
    margin-bottom: 90px;
    padding-bottom: -180px;
    display: block;
}

.cv-section-title {
    padding-top: 5px;
    font-size: 2rem;
    font-weight: 800;
    text-transform: uppercase;
    background: linear-gradient(
        90deg,
        rgb(60, 138, 145) 0%,
        rgb(255, 255, 255) 50%,
        rgb(60, 138, 145) 100%
      );
    -webkit-background-clip: text;
            background-clip: text;
    background-size: 500%;
    -webkit-text-fill-color: transparent; 
}

.cv-section-title:hover::after {
    content: ' »';
}

@media only screen and (max-width: 1500px) {
    .cv-section {
        margin-top: 225px;
        padding-top: 225px;
        margin-bottom: -225px;
    }
}
footer {
  display: flex;
  flex-direction: column;
  width: 100%;
  font-size: 2rem;
  text-transform: uppercase;
  letter-spacing: 1px;
}

footer > div:nth-child(0n + 2) {
  margin: auto auto 5px auto;
  border-top: 2px solid rgb(102, 102, 102);
  padding-top: 40px;
  width: 100%;
  text-align: center;
  font-size: 1.5rem;
}

footer > div:nth-child(0n + 3) {
  margin: 5px auto auto auto;
  font-weight: 900;
  padding-bottom: 30px;
}

footer a,
footer a:visited,
footer a:link,
footer a:active {
  text-decoration: none;
  color: #fff;
}

.footer-padder {
  height: 610px;
  background: linear-gradient(
    0deg,
    rgba(50, 116, 116, 0.3) 0%,
    rgba(0, 0, 0, 0) 100%
  );
}

.dont-hesitate::after {
  content: "";
  display: block;
  width: 100%;
  height: 1px;
  border-bottom: 2px solid white;
  transform-origin: left;
  transition: all 0.5s;
}

.dont-hesitate:hover::after {
  transform: scaleX(0);
  transition: all 0.5s;
}

@media only screen and (max-width: 1500px) {
  footer {
    margin-top: calc(0.618 * var(--footer-height) + 290px);
  }
}

